export default {
	data() {
		return {
			sizeCalcState: false,
			tabScrollTop: 0,
			currentId: 1,
			flist: [],
			slist: [],
			tlist: [],
		}
	},
	onLoad(){
		this.loadData();
	},
	methods: {
		async loadData(){
			var _this = this;
			this.$http.post('/api/goods_category/index', {}).then(res => {
				console.log(res)
				if (res.data.code != 1) {
					uni.showToast({
						title: res.data.info,
						icon: 'none'
					});
				} else {
					var data = res.data.data
					
					this.flist = data.cate;
					this.slist = data.cateTitle;
					this.tlist = data.goods;
					console.log(data.cate,data.cateTitle,data.goods)
				}
			}).catch(err => {
				console.log(err);
			})
			// let list = await this.$api.json('cateList');
			// list.forEach(item=>{
			// 	if(!item.pid){
			// 		this.flist.push(item);  //pid为父级id, 没有pid或者pid=0是一级分类
			// 	}else if(!item.picture){
			// 		this.slist.push(item); //没有图的是2级分类
			// 	}else{
			// 		this.tlist.push(item); //3级分类
			// 	}
			// }) 
		},
		//一级分类点击
		tabtap(item){
			if(!this.sizeCalcState){
				this.calcSize();
			}
			
			this.currentId = item.id;
			let index = this.slist.findIndex(sitem=>sitem.pid === item.id);
			this.tabScrollTop = this.slist[index].top;
		},
		//右侧栏滚动
		asideScroll(e){
			if(!this.sizeCalcState){
				this.calcSize();
			}
			let scrollTop = e.detail.scrollTop;
			let tabs = this.slist.filter(item=>item.top <= scrollTop).reverse();
			if(tabs.length > 0){
				this.currentId = tabs[0].pid;
			}
		},
		//计算右侧栏每个tab的高度等信息
		calcSize(){
			let h = 0;
			this.slist.forEach(item=>{
				let view = uni.createSelectorQuery().select("#main-" + item.id);
				view.fields({
					size: true
				}, data => {
					item.top = h;
					h += data.height;
					item.bottom = h;
				}).exec();
			})
			this.sizeCalcState = true;
		},
		navToList(id){
			uni.navigateTo({
				url: `/pages/product/product?id=${id}`
			})
		}
	}
}
